@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@500&display=swap);

*{
    margin:0;
}
.app__header{
    background-color: white;
    object-fit: contain;
    padding: 20px;
    border-bottom: 1px solid lightgray;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 1;
}
.app{
    background-color: #fafafa;
    height: 100vh;
}
.app__signup{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.app__header-image{
    object-fit: contain;
}
.app__posts{
    padding: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}
.app__postsRight{
        margin-left: 20px;
}
.post{
    max-width: 500px;
    background-color: white;
    border: 1px solid lightgray;
    margin-bottom: 45px;
}
.post__image{
    width:100%;
    object-fit: contain;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
}
.post__text{
    font-weight: normal;
    font-family: 'Noto Sans JP', sans-serif;
    padding: 20px;
}
.post__header{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 20px;
    font-family: 'Noto Sans JP', sans-serif;
}
.post__avatar{
    margin-right: 20px;
    
}
.post__CommentBox{
    display: -webkit-flex;
    display: flex;
    margin: 10px;
}
.post__input{
    -webkit-flex: 1 1;
            flex: 1 1;
    padding: 10px;
    border: none;
    border-top: 1px solid lightgray;
}
.post__button{
    -webkit-flex:0 1;
            flex:0 1;
    margin-left:5px;
    color: #6082a3;
    background-color: transparent;

}
.post__comments{
    padding: 10px;
    font-family: 'Noto Sans JP', sans-serif;
    font-size: 15px;
    
}
.per__comment{
    margin-bottom: 5px;
}
.imageupload{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 60%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 10px;
    margin-top: 10px;
}
.imageupload__progress{
    width: 100%;
}
